/* Start Landing Page */

.landing-page {
    position: relative;
    background-color: #FFF;
}
.landing-page .container{
    display:-webkit-flex;
    display:flex;
    padding-bottom: 0 !important;
}
.landing-page .header-area {
    display: -webkit-flex;
    display: flex;
    padding: 25px 0 0;
    position: relative;
}

.landing-page .header-area .logo {
    text-transform: uppercase;
    font-style: italic;
    margin-top: 10px;
    font-size: 19px;
    width: 300px;
    color: #5d5d5d;
}

.landing-page .header-area .links {
    list-style: none;
    padding: 0;
    margin: 0;
    width: 100%;
    text-align: right;
}

.landing-page .header-area .links li {
    display: inline-block;
    margin-left: 30px;
    color: #5d5d5d;
    cursor: pointer;
}

.landing-page .header-area .links li:last-child {
    border: 0;
    border-radius: 20px;
    padding: 10px 18px;
    color: #FFF;
    background-color: #6c63ff;
}

.landing-page .info {
    width: 50%;
    padding:100px 0 0 40px;

}

.landing-page .info h1 {
    font-size: 44px;
    margin: 0 0 20px;
    line-height: 1.4;
    color:var(--blue);
}

.landing-page .info p {
    margin: 0;
    line-height: 1.6;
    font-size: 15px;
    color:var(--blue);
}

.landing-page .info button {
    border: 0;
    border-radius: 20px;
    padding: 12px 30px;
    margin-top: 30px;
    cursor: pointer;
    color: #FFF;
    background-color: #6c63ff;
}

.landing-page .image {
    width: 50%;
    margin-bottom: -4px;
}

.landing-page .image img {
    max-width: 100%;
    border-radius:16px 0 0 16px !important;
}

.clearfix {
    clear: both;
}

@media (max-width: 768px){

    .landing-page .container{
        -webkit-flex-direction: column;
                flex-direction: column;;
    }
    .landing-page .image {
        width: 100%;
        margin-bottom: 0;
    }
    .landing-page .image img {
        border-radius:16px 16px 0 0 !important;
        height:300px;
        width:100%;
        object-fit: cover;
        object-position: center 25%;
    }
    .landing-page .info{
        padding:10px;
        width:100%;
    }
    .landing-page .info h1{
        font-size:20px;
    }
}
:root {
  /* --background: #fbf6ef;  APP BACKGROUND COLOR */
  --brown: #cfc7b8;
